<template>
  <div>
    <b-navbar type="light">
      <b-navbar-brand :to="{name:'index'}">
        Recipe site
      </b-navbar-brand>
      <b-navbar-toggle target="nav-collapse"></b-navbar-toggle>

      <b-collapse id="nav-collapse" is-nav></b-collapse>
      <b-navbar-nav>
        <b-nav-item v-if="isLoggedIn && userType" :to="admin">Admin</b-nav-item>
      </b-navbar-nav>
      <b-nav-item-dropdown right>
        <!-- Using 'button-content' slot -->
        <template v-slot:button-content>
          <em>User</em>
        </template>
        <b-dropdown-item href="#">Sign Out</b-dropdown-item>
      </b-nav-item-dropdown>
    </b-navbar>
    <main class="py-4">
      <router-view v-on:loggedin="change"></router-view>
    </main>
  </div>
</template>
<script>
  export default {
    data(){
      return {
        name        : null,
        userType   : 0,
        isLoggedIn  : localStorage.getItem('recipe-site.jwt') != null
      }
    },
    mounted() {
      this.setDefaults()
    },
    methods : {
      setDefaults(){
        if(this.isLoggedIn){
          let user        = JSON.parse(localStorage.getItem('recipe-site.user'));
          this.name       = user.name;
          this.userType  = user.isAdmin;
        }
      },
      change(){
        this.isLoggedIn = localStorage.getItem('recipe-site.jwt') != null;
        this.setDefaults();
      },
      logout(){
        localStorage.removeItem('recipe-site.jwt');
        localStorage.removeItem('recipe-site.user');
        this.change();
        this.$router.push('/');
      }
    }
  }
</script>

