import axios from 'axios';
window.axios = axios;

window.axios.defaults.headers.common['X-Requested-With'] = 'XMLHttpRequest';

import Vue from 'vue';
import Router from "vue-router";
import App from './App.vue';
import {BootstrapVue, IconsPlugin} from 'bootstrap-vue';
import 'bootstrap/dist/css/bootstrap.css'
import 'bootstrap-vue/dist/bootstrap-vue.css'

import { library } from '@fortawesome/fontawesome-svg-core';
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome';
import { faPlus } from '@fortawesome/free-solid-svg-icons';

library.add(faPlus);

Vue.component('font-awesome-icon', FontAwesomeIcon);

Vue.config.productionTip = false

Vue.use(Router);
Vue.use(BootstrapVue);
Vue.use(IconsPlugin);
import Index from "./views/Index";
import Admin from "./views/Index";
import Details from "./views/Index";

const routes = [
  {
    path: "/",
    alias: "/index",
    name: "index",
    Index
  },
  {
    path: "/recipe/:id",
    name: "tutorial-details",
    Details
  },
  {
    path: "/admin",
    name: "admin",
    Admin,
    meta:{requiresAuth: true, is_admin: true}
  }
];

const router = new Router({mode: 'history',routes: routes});


axios.interceptors.request.use(function (config)
{
  let token = localStorage.getItem('recipe-site.jwt');
  if (token)
  {
    config.headers['Authorization'] = `Bearer `+token;
  }
  return config;
}, function (error) {
  return Promise.reject(error);
});

router.beforeEach((to, from, next) => {
  if (to.matched.some(record => record.meta.requiresAuth))
  {
    if (localStorage.getItem('recipe-site.jwt') == null)
    {
      next({
        path: '/login',
        params: { nextUrl: to.fullPath }
      });
    }
    else
    {
      let user = JSON.parse(localStorage.getItem('recipe-site.user'));
      if (to.matched.some(record => record.meta.is_admin))
      {
        if (user.isAdmin === true)
          next();
        else
          next({ name: 'index' });
      }
      next();
    }
  } else
    next();
});

/* eslint-disable no-new */
new Vue({
  el: '#app',
  components: { App },
  router,
});
